body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

@font-face {
  font-family: ProximaNova-Bold;
  src: url("../fonts/ProximaNova-Bold.otf") format("opentype");
}

@font-face {
  font-family: ProximaNova-RegIt;
  src: url("../fonts/ProximaNova-RegIt.otf") format("opentype");
}

html {
  font-size: 14px;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}

a {
  text-decoration: none;
}

h2 {
  text-align: center;
  color: #1f1f1f;
  font-size: 30px;
  font-weight: 600;
  font-family: 'ProximaNova-Bold';
  margin-bottom: 50px;
}

header {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
  font-weight: 600;
  justify-content: space-between;
  margin-bottom: 80px;
  padding-top: 15px;
}

header .logo {
  width: calc(12.5% - 30px);
}

header .menu {
  width: calc(54.16666666666667% - 30px);
  display: flex;
}

header .menu ul {
  display: flex;
  align-items: center;
}

header .menu ul li {
  padding-right: 10px;
  padding-left: 10px;
}

header .menu ul li a {
  color: #494949;
}

header .menu ul li a:hover,
header .menu ul li a:active {
  color: #45b7e6;
  font-weight: 600;
}

header .contacts-mobile {
  display: none;
}

header .contacts-mobile ul {
  position: relative;
}

header .contacts-mobile ul::before {
  content: url("..\images\icons\phone.png");
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  transform: translateY(50%);
}

header .contacts-mobile ul li {
  padding-left: 30px;
}

header .contacts-mobile ul li a {
  color: #494949;
  cursor: pointer;
}

header .contacts-mobile ul li a:hover,
header .contacts-mobile ul li a:active {
  color: #45b7e6;
}

header .contacts-mobile .mail {
  position: relative;
}

header .contacts-mobile .mail a {
  padding-left: 30px;
  color: #494949;
  display: inline-block;
}

header .contacts-mobile .mail a:hover,
header .contacts-mobile .mail a:active {
  color: #45b7e6;
}

header .contacts-mobile .mail a::before {
  content: url("..\images\icons\mail.png");
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

header .contacts {
  width: calc(33.333333333333336% - 30px);
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

header .contacts .mail {
  margin-right: 25px;
}

header .contacts .mail a {
  padding-left: 30px;
  position: relative;
  color: #494949;
}

header .contacts .mail a:hover,
header .contacts .mail a:active {
  color: #45b7e6;
  font-weight: 600;
}

header .contacts .mail a::before {
  content: url("..\images\icons\mail.png");
  display: block;
  position: absolute;
}

header .contacts .phone {
  display: flex;
  flex-direction: column;
  position: relative;
}

header .contacts .phone::before {
  content: url("..\images\icons\phone.png");
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(50%);
}

header .contacts .phone a {
  margin-bottom: 2px;
  padding-left: 30px;
  position: relative;
  color: #494949;
}

header .contacts .phone a:hover,
header .contacts .phone a:active {
  color: #45b7e6;
  font-weight: 600;
}

header .illusion {
  display: none;
}

header .burger {
  display: none;
  position: absolute;
  right: 15px;
}

header .close {
  display: none;
  position: absolute;
  right: 15px;
}

