  }

  header .logo {
    padding-left: 15px;
  }

  header .menu {
    display: none;
  }

  header .menu {
    position: absolute;
  }

  header .menu {
    background: #fff;
  }

  header .menu {
    top: 30%;
  }

  header .menu {
    width: calc(100% - 30px);
  }

  header .menu {
    width: 100%;
  }

  header .menu {
    padding-bottom: 25px;
  }

  header .menu ul {
    flex-direction: column;
  }

  header .menu ul li {
    font-size: 30px;
  }

  header .menu ul li {
    margin-bottom: 15px;
  }

  header .contacts-mobile {
    width: calc(58.333333333333336% - 30px);
  }

  header .contacts-mobile {
    display: block;
  }

  header .contacts {
    display: none;
